.register-container {
    max-width: 500px;
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  h2 {
    text-align: center;
  }
  
  form div {
    margin-bottom: 15px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
  }
  
  
  textarea {
    resize: none;
  }
  
  .error {
    color: red;
    font-size: 12px;
  }
  
  button:hover {
    background-color:brown;
  }
  
  .tos-box {
    border: 1px solid #ccc;
    padding: 10px;
    margin-bottom: 15px;
    height: 250px;
    overflow-y: auto;
  }
  
  #btn-right {
    float: right;
  }